@-webkit-keyframes text-roller{
  0%{
    -webkit-transform: translateY(0px);
            transform: translateY(0px);
  }
  20%{
    -webkit-transform: translateY(0px);
            transform: translateY(0px);
  }
  25%{
    -webkit-transform: translateY(calc(-1 * var(--roller-height)));
            transform: translateY(calc(-1 * var(--roller-height)));
  }
  45%{
    -webkit-transform: translateY(calc(-1 * var(--roller-height)));
            transform: translateY(calc(-1 * var(--roller-height)));
  }
  50%{
    -webkit-transform: translateY(calc(-2 * var(--roller-height)));
            transform: translateY(calc(-2 * var(--roller-height)));
  }
  70%{
    -webkit-transform: translateY(calc(-2 * var(--roller-height)));
            transform: translateY(calc(-2 * var(--roller-height)));
  }
  75%{
    -webkit-transform: translateY(calc(-3 * var(--roller-height)));
            transform: translateY(calc(-3 * var(--roller-height)));
  }
  95%{
    -webkit-transform: translateY(calc(-3 * var(--roller-height)));
            transform: translateY(calc(-3 * var(--roller-height)));
  }
  100%{
    -webkit-transform: translateY(calc(-4 * var(--roller-height)));
            transform: translateY(calc(-4 * var(--roller-height)));
  }
}

@keyframes text-roller{
  0%{
    -webkit-transform: translateY(0px);
